/*-------------------------------Home page header-----------------------------*/
/*Header image*/
.contain {
  background-image: url("/headers/header-1.jpg");
  background-position: center bottom 40%;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  max-width: 100%;
  background-color: #313638;
}

/*Header title*/
div .headline {
  text-align: left;
  position: absolute;
  top: 15%;
  left: 54vw;
}

.headline h1 {
  color: #feb14b;
  font-family: "Playfair Display", serif;
  font-size: 5.5vw;
  text-transform: uppercase;
  letter-spacing: 3px;
}

/*Header subtitle*/
.headline p {
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  font-size: 1.17vw;
  letter-spacing: 2.5px;
}

.headline button {
  border: 1px solid #e85f5c;
  background-color: #e85f5c;
  padding: 11px 11px;
}

.headline a {
  color: white;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.1em;
}

.headline a:hover {
  color: white;
}

/*-----------------------------Home page description--------------------------*/
/*Description wrapper*/
.description {
  margin: auto;
  max-width: 85%;
  display: grid;
  padding: 100px 0px;
  grid-template-columns: 1fr 1fr;
  grid-gap: 50px;
}

/*Description left block*/
.descriptionLeft {
  grid-area: auto / span 1;
}

/*Left block text*/
.descriptionLeft h1 {
  color: #feb14b;
  font-family: "Montserrat", sans-serif;
  font-size: 4.5vw;

  font-weight: 600;
  letter-spacing: 4px;
  line-height: 100px;
  text-align: right;
}

/*Description right block*/
.descriptionRight {
  line-height: 22px;
  padding-top: 20px;
  grid-area: auto / span 1;
}

/*Right block text*/
.descriptionRight p {
  color: #313638b3;
  font-family: "Montserrat", sans-serif;
  font-size: 11pt;
  text-align: justify;
}

/*---------------------------Home page statistics-----------------------------*/

/*Background image*/
.statsBackground {
  height: 300px;
  width: 100%;
  object-fit: cover;
}

.stats {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.number {
  display: inline-grid;
  grid-template-columns: 30% 30% 45%;
  left: 0;
  margin: 0 auto;
  place-items: center center;
  position: absolute;
  right: 0;
  text-align: center;
  width: 60%;
  bottom: 30%;
}

.number span {
  color: #feb14b;
  font-family: "Montserrat", sans-serif;
  font-size: 75pt;
  font-weight: 500;
  letter-spacing: 15px;
}

.number p {
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  font-size: 14pt;
}

/*--------------Statistics Mobile Design-------------------*/
@media only screen and (max-width: 1200px) {
  .number {
    width: 70%;
  }
}
@media only screen and (max-width: 1000px) {
  .number {
    grid-template-columns: 25% 30% 45%;
    position: absolute;
    width: 85%;
  }

  .number span {
    letter-spacing: 15px;
  }
  .number p {
    font-size: 2vw;
  }
}

@media only screen and (max-width: 800px) {
  .number span {
    font-size: 12.5vw;
    letter-spacing: 8px;
  }

  .number p {
    font-size: 2.4vw;
  }

  .number {
    bottom: 33%;
    grid-template-columns: 28% 30% 45%;
    width: 85%;
  }
}

/*-------------------------------Home page events-----------------------------*/
/*Events block*/
.events {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 100px auto;
  grid-column-gap: 50px;
  padding: 100px 0px;
  max-width: 85%;
  margin: 0 auto;
}

/*Events description grid area*/
.eventsDescription {
  /* grid-area: auto / span 1; */
  /* padding-top: 10px; */
  width: 100%;
  height: 100%;
  /* height: auto; */
  padding-bottom: 80px;
}

/*Events description text*/
.eventsDescription p {
  color: #313638b3;
  font-family: "Montserrat", sans-serif;
  font-size: 11pt;
  text-align: right;
}

.eventsDescription p#about {
  padding: 20px;
  padding-bottom: 0;
}

.eventsDescription a {
  font-family: "Montserrat", sans-serif;
  text-align: right;
  color: #e85f5c;
  float: left;
}

/*Events title grid area*/
.eventsTitle {
  text-align: left;
  grid-area: 1 / 2;
}

.eventsTitle h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 55px;
  text-align: left;
}

/*Big event image grid area*/
.event1 {
  /* grid-area: 2 / 2; */
  height: 100;
}

/*Big event image size*/
.event1 img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  transition: transform 0.5s ease;
  overflow: hidden;
}

.event1:hover img {
  -ms-transform: scale(1.5); /* IE 9 */
  -webkit-transform: scale(1.5); /* Safari 3-8 */
  transform: scale(1.03);
}

.eventsDescription button {
  border: 1.5px solid #e85f5c;
  box-sizing: border-box;
  background-color: white;
  padding: 6px 6px;
}

.eventsDescription button:hover {
  border: 1.5px solid #db3d3b;
  background-color: #fff7f7;
}

.eventsDescription a {
  font-weight: 500;
}

.eventsDescription a:hover {
  color: #e85f5c;
}

/*---------------Slideshow-------------------*/

.carousel {
  text-align: center;
  /* margin: auto; */
  padding: 100px 50px;
  /* width: 85%; */
  position: relative;
}

/* .mySlides {display: none} */

.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 16px;
  margin-top: -22px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

.eventsDescription .left {
  width: 50%;
  height: 100%;
  float: left;
  padding: 21px 15px 10px 80px;
}

.eventsDescription .left p#about {
  padding: 20px;
  padding-bottom: 0;
  max-width: 100%;
  max-height: 100%;
}

.eventsDescription .right {
  width: 50%;
  height: 100%;
  float: right;
  padding: 10px 55px 10px 15px;
}

.eventsDescription .right h1 {
  text-align: left;
  grid-area: 1 / 2;
  font-family: "Montserrat", sans-serif;
  font-size: 55px;
}

.right img {
  object-fit: contain;
  max-height: 100%;
  max-width: 100%;
  transition: transform 0.5s ease;
  overflow: hidden;
}

.right a {
  font-family: "Montserrat", sans-serif;
  text-align: right;
  color: #e85f5c;
  float: left;
  height: 100%;
}

.right:hover img {
  -ms-transform: scale(1.5); /* IE 9 */
  -webkit-transform: scale(1.5); /* Safari 3-8 */
  transform: scale(1.03);
}

.carousel .buttonBack {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 0;
  height: 0;
  border-top: 17px solid transparent;
  border-bottom: 17px solid transparent;
  border-right: 30px solid #313638;
  border-left: 0;
  padding: 0;
  background-color: transparent;
  outline: 0;
}

.carousel .buttonNext {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: 0;
  height: 0;
  border-top: 17px solid transparent;
  border-bottom: 17px solid transparent;
  border-left: 30px solid #313638;
  border-right: 0;
  padding: 0;
  background-color: transparent;
  outline: 0;
}

.buttonBack:disabled {
  border-right: 30px solid #c4c4c4;
}

.buttonNext:disabled {
  border-left: 30px solid #c4c4c4;
}

.titleMobile h1 {
  display: none;
  text-align: center;
  grid-area: 1 / 2;
  font-family: "Montserrat", sans-serif;
  font-size: 55px;
}

