@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Web fonts used:
  font-family: 'Playfair Display', serif;
  font-family: 'Montserrat', sans-serif;
*/

/*Web colours*/
:root {
  --white: #fff;
  --yellow: #feb14b;
  --light-yellow: #fff7ef;
  --grey: #313638;
  --light-grey: #4f4f4f;
  --red: #e85f5c;
}

/*----------------------------------Header------------------------------------*/
/*Heading title*/
.headerTitle h1 {
  font-family: "Playfair Display", serif;
  font-size: 5vw;
  letter-spacing: 8px;
  text-transform: uppercase;
  text-align: center;
}

.coverPhoto {
  height: 100vh;
  overflow: hidden;
}

.cover_image {
  height: 100%;
  overflow: hidden;
  width: 100%;
  object-fit: cover;
  position: absolute;
}

/*website overlay*/
.coverPhoto .dark {
  width: 100%;
  background-color: #313638;
  opacity: 0.6;
  position: absolute;
  top: 0;
  height: 100vh;
  z-index: 1;
}

.coverPhoto .headerTitle {
  position: absolute;
  top: 0;
  text-align: center;
  width: 100%;
  z-index: 2;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.coverPhoto .headerTitle h1 {
  margin: 0px;
  font-weight: 600;
}

@media only screen and (max-width: 700px) {
  .coverPhoto,
  .coverPhoto .dark,
  .coverPhoto .headerTitle,
  .cover_image {
    height: 40vh;
  }

  .coverPhoto .headerTitle h1 {
    font-size: 3.5vh;
  }

  .coverPhoto .headerTitle {
    top: 25px;
  }
}

@media only screen and (max-width: 400px) {
  .coverPhoto .headerTitle h1 {
    font-size: 3vh;
  }
}

@media only screen and (max-width: 340px) {
  .coverPhoto .headerTitle h1 {
    font-size: 2.5vh;
  }
}

@media only screen and (max-width: 318px) {
  .coverPhoto {
    position: relative;
  }
}

/*----------------------------------General------------------------------------*/
h1 {
  color: #feb14b;
  font-family: "Playfair Display", serif;
  font-size: 48pt;
}

h2 {
  color: #feb14b;
  font-family: "Montserrat", sans-serif;
  font-size: 30pt;
  text-transform: uppercase;
  padding-bottom: 20px;
  text-align: center;
}

.header {
  padding-top: 50px;
  padding-bottom: 20px;
  text-align: center;
}

.subheader {
  font-family: "Montserrat", sans-serif;
  font-size: 13pt;
  color: grey;
}

p {
  font-family: "Montserrat", sans-serif;
  font-size: 10pt;
  margin-top: 0;
}

.body_bold {
  font-family: "Montserrat", sans-serif;
  font-size: 10pt;
  font-weight: bold;
}

.centre {
  text-align: center;
}

.button {
  font-family: "Montserrat", sans-serif;
  background-color: #e85f5c;
  border-radius: 60px;
  border-style: none;
  color: white;
  padding: 10px 45px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}

.button:hover {
  background-color: #feb14b;
  color: #ffffff;
}

/*-----------------------------------Navbar-----------------------------------*/
.bg-custom {
  background: transparent;
  font-size: 10pt;
  padding: 0px;
}

.nav {
  height: 70px;
}

.navbar-nav {
  color: #313638;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  text-decoration: none;
}

.nav-item {
  float: right;
  color: #313638;
  padding: 20px 5px;
  text-decoration: none;
  letter-spacing: 1.5px;
}

.nav-item-btn {
  color: #313638;
  padding-top: 22px;
  padding-right: 10px;
  display: none;
}

.nav-item a,
.dropdown-title {
  color: white;
  padding: 18px 15px;
  text-decoration: none;
}

.dropdown-menu {
  border-color: #fff7ef;
  letter-spacing: 1.5px;
  font-size: 14px;
  background-color: #fff7ef;
  margin-top: 15px;
  border-radius: 0px;
}

.dropdown-item {
  padding: 5px 0px;
}

.dropdown-item a {
  color: #313638;
}

.dropdown-item:hover {
  background-color: #fff7ef;
}

.menuTitle {
  color: #313638;
  padding: 10px 15px;
}

.menuTitle:hover {
  color: #e85f5c;
  cursor: pointer;
}

.nav-item a:hover {
  color: #e85f5c;
  padding: 18px 15px;
  text-decoration: none;
}

/*Making the navigation change colour on scroll*/
.activeNav {
  background-color: #fff7ef;
  transition: background-color 0.2s ease-in;
}

.dropdown-toggle:after {
  color: white;
}

.active-nav-item a,
.active-nav-item span,
.active-nav-item:after {
  color: #313638;
}

.logo-black {
  display: none;
}

.hiddenNav {
  display: none;
}

@media screen and (max-width: 1150px) {
  .nav-item {
    display: none;
  }

  .bg-custom {
    background-color: #fff7ef;
  }

  .photo {
    display: none;
  }

  .logo-black {
    display: block;
  }

  .nav-item-btn {
    display: block;
  }
}

/*-----------------------------------Menu-----------------------------------*/

.menu-container {
  position: fixed;
  width: 100%;
  background: #fff7ef;
  opacity: 1;
  color: #313638;
  z-index: 3;
  box-shadow: 3px 3px 5px 0 rgba(0, 0, 0, 0.2);
}

.menu-item {
  color: #313638;
  padding: 5px 35px;
  text-decoration: none;
  letter-spacing: 1.5px;
  text-transform: capitalize;
  cursor: pointer;
  font-size: 16px;
}

.menu-box:hover {
  width: 92%;
  background-color: #fff0e1c1;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
}

.menu-box:hover .menu-item {
  color: #e85f5c;
}

.menu-bar {
  float: none;
}

.menu-list {
  padding-top: 4rem;
}

.menu-dropdown {
  padding: 5px 0px;
}

/*----------------------------------Footer------------------------------------*/
footer {
  margin: 0px;
  padding: 0px;
  background-color: #fff7ef;
  min-width: 100%;
}

.footer-all {
  list-style-type: none;
  margin: 0 auto 1.4vw auto;
  padding: 4vw 0vw 2vw 0vw;
}

.footer-item {
  font-family: "Montserrat", sans-serif;
  padding: 0.33vw;
  color: #313638;
  text-transform: uppercase;
  font-size: 1.05vw;
}

.bottom-footer-item {
  font-family: "Montserrat", sans-serif;
  padding: 0.3vw;
  color: #313638;
  text-transform: uppercase;
}

.footer-logo {
  margin-top: 1%;
}

.socials .footer-icons {
  padding: 0px 0.5vw;
}

.col-width {
  flex-basis: 40%;
}

.socials {
  margin-left: 8%;
}

.socials .footer-item {
  text-align: center;
  display: flex;
  justify-content: center;
}

.footer-copyright {
  font-family: "Montserrat", sans-serif;
  text-transform: none;
  padding-bottom: 1vw;
  font-size: 1.05vw;
}

.footer-copyright,
a {
  color: #313638;
}

.footer-item a:hover {
  color: #e85f5c;
  color: var(--red);
  text-decoration: none;
}

.bottom-footer-item a:hover {
  color: #e85f5c;
  color: var(--red);
  text-decoration: none;
}

.grid-footer-container {
  display: flex;
  width: 80%;
  margin: 0px auto;
}

@media screen and (max-width: 600px) {
  .grid-footer-container {
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .footer-item {
    text-align: center;
    font-size: 16px;
  }

  .footer-copyright {
    font-size: 16px;
  }

  .footer-logo {
    margin-bottom: 2%;
  }

  .footer-icons {
    margin-right: 2vw;
  }

  .socials {
    margin-left: 0%;
    margin-top: 1%;
  }

  .footer-all {
    margin: 0vw auto 1vw auto;
  }
}

