/*-------------------------------Blog post content (text) -----------------------------*/
.blogContent {
  width: 100%;
  max-width: 950px;
  padding: 25px;
  text-align: center;
  margin: auto;
  margin-bottom: 6vw;
  word-wrap: break-word;
  hyphens: auto;
}

/*Blog title*/
.blogTitle {
  font-size: 34pt;
  letter-spacing: 4px;
  margin-top: 8vw;
}

/*Blog subtitle*/
.blogSubtitle {
  font-size: 12pt;
  color: #313638;
  text-align: center;
  margin-bottom: 2vw;
  font-style: italic;
}

/* Date and share buttons */
.blogDetails {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 3vw;
}

/*Blog content*/
.post {
  margin-bottom: 8vw;
  margin: auto;
  color: var(--light-grey);
  font-family: "Montserrat", sans-serif;
  text-align: justify;
}

p,
th,
td,
div {
  font-size: 12pt;
}

ol {
  padding-inline-start: 40px;
}

b {
  color: #000000;
  font-weight: bolder;
}

.blogDetails .date {
  color: #313638;
  font-size: 11pt;
}

.postContent {
  font-weight: 300;
}

/*Heading in post content*/
.postHeading {
  color: var(--light-grey);
  font-size: 24pt;
}

/*Subheading in post content*/
.postSubheading {
  text-decoration: underline;
}

/*Interview questions*/
.question {
  font-weight: 500;
  font-size: 12pt;
}

/*Makes dot points and numbered points larger*/
.list {
  color: var(--light-grey);
  font-family: "Montserrat", sans-serif;
  font-size: 12pt;
  text-align: justify;
  font-weight: 300;
}

/*To exaggerate words to be bigger and bolder*/
.superEmph {
  font-size: 20pt;
  font-weight: bolder;
}

.sourceLink {
  text-align: center;
  width: 100%;
}

/* Link to another page*/
.link {
  color: #e85f5c;
  text-decoration: underline;
}

.link:hover {
  color: #feb14b;
}

/*-------------------------------Blog post content (images and videos)-----------------------------*/
/*container for embedding youtube videos*/
.videoWrapper {
  display: flex;
  justify-content: center;
}

/*for any image attachment that is centre aligned*/
.pic {
  margin: 0 auto;
  max-width: 40%;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

/*for any image attachments aligned to the right*/
img.floatRight {
  float: right;
  padding: 30px;
}

.postContent a,
.outerBox a,
.pic a {
  text-decoration: underline;
}

.postContent a:hover,
.outerBox a:hover,
.pic a:hover {
  color: #feb14b;
}

.blogContent img {
  max-width: 100%;
  object-fit: contain;
  padding: 15px;
}

/*Shows multiple images in 2 columns*/
/* Example: blog-post-22 */
.outerBox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
  margin: 0 auto;
}

.smallContainer {
  width: 70%;
}

.innerBox {
  max-width: 45%;
  flex: 50%;
  text-align: center;
  padding: 15px;
}

.innerBox img {
  height: auto;
  padding: 0px;
  padding-bottom: 15px;
}

.post table {
  border-collapse: collapse;
  width: 100%;
}

.tableContainer {
  overflow-x: auto;
}

.post th,
.post td {
  padding: 10px;
  text-align: left;
}

.post th {
  background-color: #feb14b;
  color: white;
  font-weight: 500;
}

/*-------------------------------Author card-----------------------------*/
.author {
  margin: 0 auto;
  margin-bottom: 4vw;
  /*margin-bottom: 6vw;*/
  display: flex;
  align-items: center;
  width: 450px;
}

/*Author icon*/
.authorLeft {
  display: inline-block;
  width: 150px;
  height: 150px;
}

.authorLeft div {
  border-radius: 50%;
  height: 100%;
  overflow: hidden;
  width: 100%;
}

/*Profile pictures*/
.authorProfile {
  height: auto;
  transform: translate(-5%, -10%);
  width: 120%;
}

.anonProfile {
  height: auto;
  width: 100%;
}

/*Author details*/
.authorRight {
  display: inline-block;
  padding: 0px 0px 0px 30px;
  letter-spacing: 2px;
  color: #feb14b;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  text-align: left;
}

.authorName {
  font-family: "Playfair Display", serif;
  font-size: 20pt;
  margin: 0;
  padding: 0;
}

.position {
  margin-top: 5px;
  font-size: 14pt;
  color: #e85f5c;
}

.authorRight p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: var(--light-grey);
  font-size: 1vw;
  margin-top: 10px;
}

/*-------------------------------Blog mobile version-----------------------------*/
@media only screen and (max-width: 800px) {
  .videoWrapper {
    position: relative;
    padding-bottom: 56.25%; /* 16:9 */
    padding-top: 25px;
    height: 0;
  }

  .videoWrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

@media only screen and (max-width: 700px) {
  .outerBox {
    flex-direction: column;
  }

  .smallContainer {
    width: 100%;
    flex-direction: row;
  }
}

@media only screen and (max-width: 600px) {
  .blogTitle {
    font-size: 20pt;
  }

  .postHeading {
    font-size: 18pt;
  }

  .blogSubtitle {
    font-size: 12pt;
  }

  p {
    font-size: 12pt;
  }

  .author {
    flex-flow: column;
    width: 100%;
  }

  .authorRight {
    text-align: center;
    padding: 5% 0% 0% 0%;
  }

  .authorName {
    font-size: 16.5pt;
  }

  .position {
    font-size: 12pt;
  }

  .blogContent img {
    height: auto;
    padding: 5px;
  }
}

@media only screen and (max-width: 400px) {
  .blogDetails span {
    display: block;
  }

  .blogDetails span:last-child {
    padding: 10px 0px;
  }

  .blogDetails {
    display: block;
  }

  .smallContainer {
    flex-direction: column;
  }

  .blogTitle {
    font-size: 16.5pt;
  }

  .postHeading {
    font-size: 16.5pt;
  }

  .pic,
  .innerBox {
    max-width: 100%;
  }

  img.floatRight {
    float: none;
  }

  .blogContent p:not(.date),
  .blogContent li {
    text-align: justify;
  }
}
