#container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

#image404 {
  margin: 0 auto;
  object-fit: contain;
  width: 45vw;
}

#homeBtn {
  background-color: #e85f5c;
  color: white;
  text-transform: uppercase;
  padding: 20px 25px;
  font-size: 18pt;
  letter-spacing: 1.5px;
  margin-top: 20px;
}

@media only screen and (max-width: 500px) {
  #container {
    width: 80%;
    margin: 0 auto;
  }

  h2 {
    font-size: 22pt;
  }

  p {
    font-size: 12pt;
  }

  #homeBtn {
    font-size: 16pt;
  }
}
