/*-------------------------------Opportunities Preview-----------------------------*/

.head {
  font-family: "Montserrat", sans-serif;
  font-size: 11pt;
  font-weight: 400;
  color: #313638b3;
  text-align: justify;
  letter-spacing: 0.1em;
}

.link {
  color: #313638;
  font-weight: 500;
}

.link:hover {
  color: #feb14b;
}

.oppBody {
  max-width: 70%;
  margin: 0 auto;
  margin-bottom: 10%;
  margin-top: 4%;
}

.oppLookout {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  margin: 0 auto;
  padding: 100px 0;
  max-width: 70%;
}

.lookoutSize {
  font-size: 12pt;
}

.oppGridContainer {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
  grid-gap: 50px;
  margin-top: 50px;
}

.oppGridItems {
  display: block;
  position: relative;
  box-shadow: 5px 5px 8px 0 rgba(0, 0, 0, 0.3);
}

.oppDesc {
  margin: 50px 30px;
  padding: 0;
}

.oppTypeAndLocation {
  color: #feb14b;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 1pt;
  margin: 0;
  padding: 0;
  padding-bottom: 5px;
  text-align: center;
  font-weight: 500;
  font-size: 10pt;
}

.oppSummary {
  color: var(--light-grey);
  letter-spacing: 0.05em;
  font-size: 9pt;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  padding: 0;
  padding-bottom: 5px;
  margin-top: 10px;
  text-align: justify;
}

.oppImg {
  object-fit: scale-down;
  display: block;
  margin: 0 auto;
  width: 250px;
  height: 150px;
  transform: translate(0%, 20%);
}

.jobPosition {
  color: #313638;
  font-size: 14pt;
  font-family: "Playfair Display", serif;
  font-weight: 500;
  text-align: center;
  margin-bottom: 10px;
}

/*---------------------------Loading Sign--------------------------*/
#oppLoading {
  color: #feb14b;
  margin-top: 5%;
  margin-bottom: 5%;
}

#oppLoadingContainer {
  display: flex;
  justify-content: center;
}

/*-------------------------------Responsive mobile design------------------------------*/
@media only screen and (max-width: 750px) {
  .head,
  .lookoutSize {
    font-size: 10pt;
  }

  .oppLookout {
    padding: 50px 0;
  }
}

@media only screen and (max-width: 640px) {
  .oppBody,
  .oppLookout {
    max-width: 90%;
  }

  .head {
    max-width: 80%;
    margin: 0 auto;
  }

  .oppGridContainer {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  }
}

@media only screen and (max-width: 400px) {
  .oppBody {
    max-width: 100%;
  }

  .oppGridContainer {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }

  .lookoutSize {
    font-size: 9pt;
  }
}

@media only screen and (max-width: 350px) {
  .oppImg {
    width: 250px;
    height: 150px;
  }
}
