/*CAN REMOVE ONCE OVERLAY FILES ARE ALL MERGED*/
/* direct copy from what will be in style.css from other branch */
.overlay {
  /* overlay on all header images*/
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #313638;
  opacity: 0.5;
}

/*-------------------------------Publications structure------------------------------*/
/* Subheading */
.publicationsBody h1 {
  font-family: "Montserrat", sans-serif;
}

/*Body block*/
.publicationsBody {
  max-width: 70%;
  margin: auto;
  padding-top: 80px;
  padding-bottom: 80px;
}

.row {
  margin-top: 20px;
  margin-bottom: 50px;
  height: auto;
}

/*Right side block*/
.article {
  display: inline-block;
  width: 212px;
  height: 300px;
  text-align: center;
  margin: 0px 28px 0px 0px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  position: relative;
  margin-top: 30px;
  overflow: hidden;
}

/*-------------------------------Publications Article------------------------------*/

.imageContainer {
  position: absolute;
  z-index: 0;
}

.articleImg {
  height: 100%;
  width: 100%;
  object-fit: cover;
  position: relative;
}

.article .dark {
  width: 100%;
  background-color: black;
  opacity: 0;
  position: absolute;
  top: 0;
  height: 100%;
  z-index: 1;
  visibility: hidden;
  transition: opacity 0.5s ease;
}

.article:hover h2,
.article:hover .dark,
.article:hover p,
.article:hover .button {
  visibility: visible;
}

.article:hover .dark {
  opacity: 0.5;
}

.textContainer {
  position: absolute;
  left: 50%;
  z-index: 2;
  top: 70px;
}

.article h2 {
  left: -50%;
  text-transform: capitalize;
  text-align: center;

  font-size: 21px;
  display: block;
  visibility: hidden;
  position: relative;
  padding: 5px;
}

.article p {
  font-size: 15px;
  color: white;
  font-family: "Montserrat", sans-serif;
  display: block;
  text-align: center;
  visibility: hidden;
  float: center;
  left: -50%;
  position: relative;
}

/*read only button, change for functionality*/
.button {
  background-color: #e85f5c;
  height: 30px;
  width: 120px;
  visibility: hidden;
  left: -50%;
  position: relative;
  border-radius: 60px;
}

.button h3 {
  font-size: 10px;
  font-weight: 500px;
  color: white;
  text-transform: uppercase;
  white-space: nowrap;
  float: center;
  line-height: 30px;
  letter-spacing: 0.1vw;
}

.button a,
.button a:hover {
  text-decoration: none;
  color: white;
}

/*-------------------------------Loading Sign-----------------------------*/
#pubLoading {
  color: #feb14b;
  margin-top: 10%;
  margin-bottom: 7%;
}

#pubLoadingContainer {
  display: flex;
  justify-content: center;
}

/* ------------ Responsive mobile design ------------ */

@media only screen and (max-width: 850px) {
  .article {
    margin: 15px 15px;
    width: 178px;
    height: 250px;
  }

  .article .dark {
    margin: 15px 15px;
    width: 178px;
    height: 250px;
  }
}
